import React, { Suspense } from 'react';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { CircularProgress, Box } from '@mui/material';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import ErrorBoundary from './components/common/ErrorBoundary';
import NetworkStatusMonitor from './components/common/NetworkStatusMonitor';
import { AuthProvider } from './contexts/AuthContext';
import jwtDecode from 'jwt-decode';

// Define the type for JWT token payload
interface JwtPayload {
  exp?: number;
  sub?: string;
  roles?: string[];
  [key: string]: any;
}

// Lazy load the AppRouter to ensure auth context is initialized first
const AppRouter = React.lazy(() => import('./AppRouter'));

const theme = createTheme({
    palette: {
        mode: 'light',
        primary: {
            main: '#9C27B0', // Purple
            light: '#E33069', // Pink (updated from #FF5F8F)
            dark: '#7B1FA2', // Dark purple
            contrastText: '#ffffff',
        },
        secondary: {
            main: '#FF7F23', // Orange (updated from #FF8C37)
            light: '#FFAA66',
            dark: '#E67D2E',
            contrastText: '#ffffff',
        },
        background: {
            default: '#f9f9f9',
            paper: '#FFFFFF',
        },
        text: {
            primary: '#333333',
            secondary: '#666666',
        },
        error: {
            main: '#f44336',
        },
        warning: {
            main: '#ff9800',
        },
        info: {
            main: '#2196f3',
        },
        success: {
            main: '#4caf50',
        },
    },
    shape: {
        borderRadius: 8,
    },
    components: {
        MuiButton: {
            styleOverrides: {
                root: {
                    borderRadius: 8,
                    textTransform: 'none',
                },
                containedPrimary: {
                    backgroundColor: '#FF7F23', // Updated from #FF8C37
                    '&:hover': {
                        backgroundColor: '#E67D2E',
                    },
                },
            },
        },
        MuiCard: {
            styleOverrides: {
                root: {
                    borderRadius: 8,
                    boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.1)',
                },
            },
        },
        MuiTextField: {
            styleOverrides: {
                root: {
                    '& .MuiOutlinedInput-root': {
                        borderRadius: 8,
                    },
                },
            },
        },
        MuiAlert: {
            styleOverrides: {
                root: {
                    borderRadius: 8,
                },
            },
        },
    },
});

function App() {
    // Clear any potential stale tokens on initial load if they're expired
    React.useEffect(() => {
        try {
            const token = localStorage.getItem('token');
            
            if (token) {
                const decoded = jwtDecode<JwtPayload>(token);
                const currentTime = Date.now() / 1000;
                
                if (!decoded || !decoded.exp || decoded.exp < currentTime) {
                    console.log('App - Removing expired token on startup');
                    localStorage.removeItem('token');
                }
            }
        } catch (error) {
            console.error('App - Error checking token on startup:', error);
            localStorage.removeItem('token');
        }
    }, []);

    return (
        <ErrorBoundary>
            <ThemeProvider theme={theme}>
                <AuthProvider>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                        <CssBaseline />
                        <Suspense 
                            fallback={
                                <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
                                    <CircularProgress />
                                </Box>
                            }
                        >
                            <AppRouter />
                        </Suspense>
                        <NetworkStatusMonitor />
                    </LocalizationProvider>
                </AuthProvider>
            </ThemeProvider>
        </ErrorBoundary>
    );
}

export default App;